import React, {
  FunctionComponent,
  useContext,
  PropsWithChildren,
  Dispatch,
  useReducer,
  useEffect,
} from 'react'
import {
  State,
  Value,
  Action,
  splatReducer,
  Middleware,
  applyMiddleware,
  setProcessState,
  StateWithContext,
  startProcess,
  setProcessTypeData,
} from '~/lib/reducers'
import { get } from 'lodash'
import debugWith from 'debug'
import { SplatBackend, Message, ProcessTypeData } from '~/lib/backend'
import { filter } from 'rxjs/operators'
import {
  backendCommunicationMiddlewareFactory,
  isTypeEventWithState,
} from '~/lib/backend-middleware'

const debug = debugWith('splat:process')

interface ComponentProps {
  name: string
  state?: StateWithContext
  middleware?: Middleware[]
  backend?: SplatBackend
  params?: Record<string, unknown>
}

type Props = PropsWithChildren<ComponentProps>

// produce a value of type V, as if by magic
export type Summon<V> = () => V

export const lookupField = (state: State, path: string): Value =>
  get(state, path)

export class ContextMissingError implements Error {
  constructor(message: string) {
    this.message = message
    this.name = 'ContextMissingError'
    Error.captureStackTrace(this, ContextMissingError)
  }
  name: string
  message: string
  stack?: string | undefined
}

const defaultDispatcher: Dispatch<Action> = (action: Action) => {
  debug('default dispatcher called with action', action)
  throw new ContextMissingError(
    "no SplProcessDispatchCtx in scope. Perhaps you don't have a parent SplProcess?"
  )
}

const SplProcessTypeNameCtx: React.Context<string> = React.createContext('???')
const SplProcessTypeDataCtx: React.Context<ProcessTypeData> = React.createContext<
  ProcessTypeData
>(null)
const SplProcessStateCtx: React.Context<State> = React.createContext({})
const SplProcessDispatchCtx: React.Context<Dispatch<
  Action
>> = React.createContext(defaultDispatcher)

export const useSplatProcessTypeName: Summon<string> = () =>
  useContext(SplProcessTypeNameCtx)
export const useSplatProcessTypeData: Summon<ProcessTypeData> = () =>
  useContext(SplProcessTypeDataCtx)
export const useSplatProcessState: Summon<State> = () =>
  useContext(SplProcessStateCtx)

export const useSplatProcessDispatch: Summon<Dispatch<Action>> = () =>
  useContext(SplProcessDispatchCtx)

export const SplProcess: FunctionComponent<Props> = (props: Props) => {
  debug('process component created with backend', !!props.backend)

  const [stateWithContext, dispatch] = useReducer(
    splatReducer,
    props.state || { state: {}, typeData: null }
  )

  const backendMiddleware = backendCommunicationMiddlewareFactory(
    stateWithContext,
    props.backend
  )

  const dispatchWithMiddleware = applyMiddleware(
    dispatch,
    [backendMiddleware].concat(props.middleware || [])
  )

  useEffect(() => {
    if (props.backend && props.name && !stateWithContext.instanceUri) {
      const [subsystem, name] = props.name.split('.')
      debug('no instanceUri, starting new process', subsystem, name)
      dispatchWithMiddleware(startProcess(subsystem, name, props.params))
    }
  }, [
    dispatchWithMiddleware,
    props.backend,
    props.name,
    props.params,
    stateWithContext.instanceUri,
  ])

  useEffect(() => {
    if (props.backend && stateWithContext.instanceUri) {
      debug('subscribing to messages')
      const relevantMessages = (message: Message) =>
        isTypeEventWithState(message) &&
        stateWithContext.instanceUri == message.event?.origin

      const subscription = props.backend
        .messageBox()
        .pipe(filter(relevantMessages))
        .subscribe((message) => {
          if (message.typeData) {
            dispatchWithMiddleware(setProcessTypeData(message.typeData))
          }
          dispatchWithMiddleware(setProcessState(message.state))
        })
      return () => subscription.unsubscribe()
    }
  }, [dispatchWithMiddleware, props.backend, stateWithContext.instanceUri])

  return (
    <SplProcessTypeDataCtx.Provider value={stateWithContext.typeData}>
      <SplProcessTypeNameCtx.Provider value={props.name}>
        <SplProcessStateCtx.Provider value={stateWithContext.state}>
          <SplProcessDispatchCtx.Provider value={dispatchWithMiddleware}>
            {props.children}
          </SplProcessDispatchCtx.Provider>
        </SplProcessStateCtx.Provider>
      </SplProcessTypeNameCtx.Provider>
    </SplProcessTypeDataCtx.Provider>
  )
}
