import {
  StateWithContext,
  Middleware,
  Dispatcher,
  Action,
  setProcessState,
  setProcessContext,
  setProcessTypeData,
  InvokeAction,
} from './reducers'
import { SplatBackend, Message } from './backend'
import { filter } from 'rxjs/operators'
import debugWith from 'debug'
import { ActionResponse } from '~/SplatComponents/SplAction'
const debug = debugWith('splat:backend:middleware')

export const isTypeEventWithState = (message: Message): boolean =>
  message.type == 'TypeEventWithState'

export const backendCommunicationMiddlewareFactory = (
  state: StateWithContext,
  backend?: SplatBackend
): Middleware => {
  debug(
    'creating backend middleware with backend',
    !!backend,
    state.instanceUri,
    state.lastKnownEventId
  )
  return (next: Dispatcher) => (action: Action): void => {
    debug(
      'got action',
      action,
      'has backend',
      !!backend,
      state.instanceUri,
      state.lastKnownEventId
    )
    if (!backend) {
      return next(action)
    }
    switch (action.type) {
      case 'start-process':
        backend
          .startProcess(
            action.subsystem as string,
            action.name as string,
            action.params as Record<string, unknown> | undefined
          )
          .pipe(filter(isTypeEventWithState))
          .subscribe((message) => {
            next(
              setProcessContext(message.event.origin, message.lastKnownEventId)
            )
            // The process-level subscription doesn't activate in time to see this TypeEventWithState-message
            if (message.typeData) {
              next(setProcessTypeData(message.typeData))
            }
            next(setProcessState(message.state))
          })
        return
      case 'invoke-action':
        if (state.instanceUri) {
          const actionInvocation = action as InvokeAction
          const subscription = backend
            .executeAction(
              state.instanceUri,
              action.name as string,
              action.params as Record<string, unknown> | undefined
            )
            .subscribe((message) => {
              // ? We could move the message type filter and cast to a custom RxJs operator
              // See https://medium.com/ngconf/filtering-types-with-correct-type-inference-in-rxjs-f4edf064880d
              if (message.type === 'ActionCompleted') {
                // TODO: verify msg.event.actionName ?
                // TODO: what about msg.event.notifications ?
                if (actionInvocation.deferredResponse) {
                  actionInvocation.deferredResponse.resolve(
                    message.event.returnValue as ActionResponse
                  )
                }

                subscription.unsubscribe()
              }
            })
        } else {
          throw new InstanceUnknownError(
            'The process instance is not yet known, cannot execute action'
          )
        }
        break
      case 'update':
        if (state.instanceUri) {
          if (state.lastKnownEventId) {
            backend.updateProcess(
              state.instanceUri,
              action.path as string,
              action.value as string,
              state.lastKnownEventId
            )
          } else {
            throw new InstanceUnknownError(
              'We have no known event id, cannot update'
            )
          }
        } else {
          throw new InstanceUnknownError(
            'The process instance is not yet known, cannot update'
          )
        }
        break
      default:
        break
    }
    next(action)
  }
}

export class InstanceUnknownError implements Error {
  constructor(message: string) {
    this.message = message
    this.name = 'InstanceUnknownError'
    Error.captureStackTrace(this, InstanceUnknownError)
  }
  name: string
  message: string
  stack?: string | undefined
}

/** The deferred class is an 'inverted' promise, which can be created at one place,
 * and resolved (or rejected) elsewhere
 * */

export class Deferred<T> {
  resolve!: (value?: T | PromiseLike<T>) => void
  reject!: (reason?: unknown) => void
  promise: Promise<T>

  constructor() {
    this.promise = new Promise((resolve, reject) => {
      this.resolve = resolve
      this.reject = reject
    })
  }
}
