import { v4 as uuid } from 'uuid'
import {
  Observable,
  Subject,
  SchedulerAction,
  asyncScheduler,
  Subscription,
} from 'rxjs'
import { AsyncScheduler } from 'rxjs/internal/scheduler/AsyncScheduler'
import { State, Value } from './reducers'
import { IServerTypeData } from './backend-types'
import { filter } from 'rxjs/operators'
import debugWith from 'debug'
const debug = debugWith('splat:backend')

export type StartProcessPayload = {
  type: string
  data?: Record<string, unknown>
}

export const startProcessPayload = (
  subsystem: string,
  tenantId: string,
  processName: string,
  params?: Record<string, unknown>
): StartProcessPayload => ({
  type: `spl://${subsystem}/${tenantId}/${processName}`,
  data: params,
})

export type ExecuteActionPayload = {
  instance: string
  actionName: string
  arguments?: Record<string, unknown>
}

export const executeActionPayload = (
  instanceUri: string,
  actionName: string,
  params?: Record<string, unknown>
): ExecuteActionPayload => ({
  instance: instanceUri,
  actionName,
  arguments: params,
})

export type UpdateProcessPayload = {
  action: 'update'
  currentEventId: string
  instance: string
  property: string
  value: Value
}

export const updateProcessPayload = (
  instanceUri: string,
  propertyPath: string,
  value: Value,
  currentEventId: string
): UpdateProcessPayload => ({
  action: 'update',
  currentEventId,
  instance: instanceUri,
  property: propertyPath,
  value: value,
})

export const commandUrl = (
  host: string,
  subsystem: string,
  tenantId: string,
  commandName: string
): string => `${host}/${subsystem}/${tenantId}/command/${commandName}`

export const messagesUrl = (
  host: string,
  frontendSubsystem: string,
  tenantId: string
): string => `${host}/${frontendSubsystem}/${tenantId}/messages`

export type HttpRequest = {
  method: string
  url: string
  queryParams: Record<string, string>
  body?: Record<string, unknown>
}

export const queryString = (queryParams?: Record<string, string>): string =>
  queryParams && Object.entries(queryParams).length > 0
    ? '?' +
      Object.entries(queryParams)
        .map(
          ([key, val]) =>
            `${encodeURIComponent(key)}=${encodeURIComponent(val)}`
        )
        .join('&')
    : ''

export type Transport = (
  r: HttpRequest,
  responseFormat: string
) => Promise<HttpResponse>
export type HttpResponse = string | Record<string, unknown>

export const constructPostRequest = (
  baseUrl: string,
  queryParams: Record<string, string>,
  body: Record<string, unknown>
): HttpRequest => ({
  method: 'POST',
  url: baseUrl,
  queryParams,
  body,
})

export type MessageResponse = {
  lastSequenceNumber: number
  messages: Message[]
}

export type ProcessTypeData = Record<string, IServerTypeData> | null

export type Message = {
  lastKnownEventId: string
  type: string
  typeData: ProcessTypeData
  state: State
  event: {
    origin: string
    initiator: { correlationId: string }
    // support arbitrary event properties
    [propName: string]: unknown
  }
}

interface Repeater<T> {
  (this: SchedulerAction<T>, state?: T): void
}

export const repeatAsyncTask = <T>(
  task: (
    some: T | undefined,
    scheduler?: AsyncScheduler
  ) => Promise<T | undefined>,
  {
    repeatDelay = 10,
    errorDelay = 1000,
    scheduler,
    errorHandler = (reason: unknown) => console.error(reason),
  }: PollingConfig
): Repeater<T> => {
  // an arrow function cannot have a 'this' parameter, so this is a regular function
  function asyncRepeater(this: SchedulerAction<T>, state?: T): void {
    // eslint-disable-next-line jest/valid-expect-in-promise
    task(state, scheduler)
      .then((newState) => {
        // `this` references currently executing Action,
        // which we reschedule with new state and delay
        this.schedule(newState, repeatDelay)
        return newState
      })
      .catch((reason) => {
        errorHandler(reason)
        // TODO: Consider if we should reuse the same state again, or not
        this.schedule(state, errorDelay)
      })
  }
  return asyncRepeater
}

type PollingConfig = {
  initialDelay?: number
  repeatDelay?: number
  errorDelay?: number
  scheduler?: AsyncScheduler
  errorHandler?: (reason: unknown) => void
}

export const startMessagePolling = (
  backend: SplatBackend,
  {
    initialDelay = 0,
    repeatDelay = 10,
    errorDelay = 1000,
    scheduler = asyncScheduler,
    errorHandler = (reason: unknown) => console.error(reason),
  }: PollingConfig = {}
): Subscription => {
  const poll = () => {
    return backend.getMessages()
  }
  return scheduler.schedule(
    repeatAsyncTask(poll, { repeatDelay, errorDelay, errorHandler, scheduler }),
    initialDelay
  )
}

export class SplatBackend {
  readonly clientId: string
  readonly tenantId: string
  readonly frontendSubsystem: string
  readonly host: string
  readonly transport: Transport
  lastCorrelationId: string
  lastSequenceNumber: number
  readonly correlationBase: string
  readonly messageBoxSubject: Subject<Message>

  messageId = 1

  constructor(
    host: string,
    frontendSubsystem: string,
    tenantId: string,
    transport: Transport
  ) {
    // TODO: Maybe use sessionStorage so we don't generate a new clientId when reloading the page
    this.clientId = uuid()
    this.host = host
    this.frontendSubsystem = frontendSubsystem
    this.tenantId = tenantId
    this.transport = transport
    this.correlationBase = this.clientId.substring(0, 8)
    this.lastCorrelationId = ''
    this.lastSequenceNumber = -1
    this.messageBoxSubject = new Subject<Message>()
  }

  private commandUrl(subsystem: string, commandName: string): string {
    return commandUrl(this.host, subsystem, this.tenantId, commandName)
  }

  private generateCorrelationId(): string {
    return (this.lastCorrelationId =
      this.correlationBase + ':' + this.messageId++)
  }

  helloServer(): Observable<Message> {
    const correlationId = this.generateCorrelationId()
    this.transport(this.helloServerRequest(correlationId), 'string')
    // TODO: Handle transport errors
    return this.repliesWith(correlationId)
  }

  private helloServerRequest(correlationId: string): HttpRequest {
    return this.commandRequest(
      this.frontendSubsystem,
      'hello-server',
      correlationId,
      {}
    )
  }

  startProcess(
    subsystem: string,
    processName: string,
    params?: Record<string, unknown>
  ): Observable<Message> {
    const correlationId = this.generateCorrelationId()
    this.transport(
      this.startProcessRequest(subsystem, processName, correlationId, params),
      'string'
    )
    // TODO: Handle transport errors
    return this.repliesWith(correlationId)
  }

  private startProcessRequest(
    subsystem: string,
    processName: string,
    correlationId: string,
    params?: Record<string, unknown>
  ): HttpRequest {
    const body = startProcessPayload(
      subsystem,
      this.tenantId,
      processName,
      params
    )
    return this.commandRequest(
      subsystem,
      'start-process',
      correlationId,
      (body as unknown) as Record<string, unknown>
    )
  }

  executeAction(
    instanceUri: string,
    actionName: string,
    params?: Record<string, unknown>
  ): Observable<Message> {
    const correlationId = this.generateCorrelationId()
    this.transport(
      this.executeActionRequest(instanceUri, actionName, correlationId, params),
      'string'
    )
    // TODO: Handle transport errors
    return this.repliesWith(correlationId)
  }

  private executeActionRequest(
    instanceUri: string,
    actionName: string,
    correlationId: string,
    params?: Record<string, unknown>
  ): HttpRequest {
    const body = executeActionPayload(instanceUri, actionName, params)
    const subsystem = instanceUri.split('/')[2]
    return this.commandRequest(
      subsystem,
      'execute-action',
      correlationId,
      (body as unknown) as Record<string, unknown>
    )
  }

  updateProcess(
    instanceUri: string,
    propertyPath: string,
    value: Value,
    currentEventId: string
  ): Observable<Message> {
    const correlationId = this.generateCorrelationId()
    this.transport(
      this.updateProcessRequest(
        instanceUri,
        propertyPath,
        value,
        currentEventId,
        correlationId
      ),
      'string'
    )
    // TODO: Handle transport errors
    return this.repliesWith(correlationId)
  }

  private updateProcessRequest(
    instanceUri: string,
    propertyPath: string,
    value: Value,
    currentEventId: string,
    correlationId: string
  ): HttpRequest {
    const body = updateProcessPayload(
      instanceUri,
      propertyPath,
      value,
      currentEventId
    )
    const subsystem = instanceUri.split('/')[2]
    return this.commandRequest(
      subsystem,
      'update-process',
      correlationId,
      (body as unknown) as Record<string, unknown>
    )
  }

  private commandRequest(
    subsystem: string,
    commandName: string,
    correlationId: string,
    body: Record<string, unknown>
  ): HttpRequest {
    const url = this.commandUrl(subsystem, commandName)
    debug('command request', subsystem, commandName)
    const request = constructPostRequest(
      url,
      { clientId: this.clientId, correlationId },
      body
    )
    return request
  }

  getMessages(): Promise<void> {
    const pollRequest: HttpRequest = {
      method: 'GET',
      url: messagesUrl(this.host, this.frontendSubsystem, this.tenantId),
      queryParams: {
        clientId: this.clientId,
        last: this.lastSequenceNumber.toString(),
      },
    }
    return this.transport(pollRequest, 'json').then((response) => {
      const messageResponse = response as MessageResponse
      this.lastSequenceNumber = messageResponse.lastSequenceNumber
      this.publishMessages(messageResponse)
    })
  }

  publishMessages(messageResponse: MessageResponse): void {
    messageResponse.messages.forEach((message) => {
      this.messageBoxSubject.next(message)
    })
  }

  messageBox(): Observable<Message> {
    return this.messageBoxSubject
  }

  repliesWith(correlationId: string): Observable<Message> {
    return this.messageBoxSubject.pipe(
      filter(
        (message) => message.event?.initiator?.correlationId == correlationId
      )
    )
  }
}
