import React, { PropsWithChildren, FunctionComponent, useContext } from 'react'
import { Value, updateField } from '~/lib/reducers'
import {
  useSplatProcessState,
  useSplatProcessDispatch,
  lookupField,
  Summon,
} from './SplProcess'

type UpdateValueFn = (v: Value) => void
export const useSplatField: (path?: string) => [Value, UpdateValueFn] = (
  path?: string
) => {
  const state = useSplatProcessState()
  // the rules of Hooks state that we must call them in the exact same
  // order on every component render so we _must_ call useSplatFieldPath
  // unconditionally even though the caller may have provided a path
  const pathFromCtx = useSplatFieldPath()
  const usedPath = path || pathFromCtx
  const value = lookupField(state, usedPath)
  const dispatch = useSplatProcessDispatch()
  const handler = (value: Value) => dispatch(updateField(usedPath, value))
  return [value, handler]
}

interface ComponentProps {
  path: string
}

type Props = PropsWithChildren<ComponentProps>

const SplFieldPathCtx: React.Context<string> = React.createContext('')
export const useSplatFieldPath: Summon<string> = () =>
  useContext(SplFieldPathCtx)

export const SplField: FunctionComponent<Props> = (props: Props) => {
  return (
    <SplFieldPathCtx.Provider value={props.path}>
      {props.children}
    </SplFieldPathCtx.Provider>
  )
}
