import produce from 'immer'
import debugWith from 'debug'
import { set, reduce } from 'lodash'
import { ProcessTypeData } from './backend'
import { Deferred } from './backend-middleware'
import { ActionResponse } from '~/SplatComponents/SplAction'

const debug = debugWith('splat:reducer')

export type Value = string | number | boolean | Record<string, unknown>
export type State = Record<string, Value>

export type StateWithContext = {
  instanceUri?: string
  lastKnownEventId?: string
  typeData: ProcessTypeData
  state: State
}

export interface Action {
  readonly type: string
  [propName: string]: unknown
}

export type Reducer<S> = (s: S, a: Action) => S
export type Dispatcher = (a: Action) => void
/**
 * Middleware are dispatcher functions that intercept an action sent to the
 * dispatcher. They sit in the middle between the caller and another dispatcher.
 * Middleware can inspect, modify, or log the action as they see fit. They can
 * pass the action on to the next dispatcher in the chain, or simply discard it.
 * Or, they could do async work and then eventually call the next dispatcher.
 */
export type Middleware = (nextDispatcher: Dispatcher) => Dispatcher

export function generateDispatcher<S>(
  reducer: Reducer<S>,
  initializerArg: S
): [S, Dispatcher] {
  let state = initializerArg
  const dispatcher = (action: Action) => {
    const newState = reducer(state, action)
    debug('state transition', action.type, state, newState)
    state = newState
  }
  return [state, dispatcher]
}

/** applyMiddleware decorates the dispatcher with additional middleware.
 * The last middleware passed in, becomes the top one, invoked by the caller.
 * The first middleware passed in, becomes the bottom one, calling the
 * original dispatcher.
 *
 * Middleware are dispatcher functions that intercept an action sent to the
 * dispatcher. They sit in the middle between the caller and another dispatcher.
 * Middleware can inspect, modify, or log the action as they see fit. They can
 * pass the action on to the next dispatcher in the chain, or simply discard it.
 * Or, they could do async work and then eventually call the next dispatcher.
 */
export const applyMiddleware = (
  dispatch: Dispatcher,
  middleware?: Middleware[]
): Dispatcher => {
  dispatch = reduce(middleware, connectMiddleware, dispatch)
  return dispatch
}

const connectMiddleware = (next: Dispatcher, current: Middleware) =>
  current(next)

// This is an action creator, it only _creates_ the action!
export const updateField = (path: string, value: Value): Action => ({
  type: 'update',
  path,
  value,
})

export interface InvokeAction extends Action {
  type: string
  name: string
  deferredResponse?: Deferred<ActionResponse>
  params?: Record<string, unknown>
}

// This is an action creator, it only _creates_ the action!
// Beware of the terminology clash:
// - These actions are dispatched to a reducer to affect the state
// - This particular reducer action invokes a Splat DSL Action
export const invokeAction = (
  actionName: string,
  deferredResponse?: Deferred<ActionResponse>,
  params?: Record<string, unknown>
): InvokeAction => ({
  type: 'invoke-action',
  name: actionName,
  deferredResponse,
  params,
})

// This is an action creator, it only _creates_ the action!
export const setProcessState = (state: State): Action => ({
  type: 'set-process-state',
  state,
})

// This is an action creator, it only _creates_ the action!
export const setProcessContext = (
  instanceUri: string,
  lastKnownEventId: string
): Action => ({ type: 'set-process-context', instanceUri, lastKnownEventId })

// This is an action creator, it only _creates_ the action!
export const startProcess = (
  subsystem: string,
  name: string,
  params?: Record<string, unknown>
): Action => ({
  type: 'start-process',
  subsystem,
  name,
  params,
})

// This is an action creator, it only _creates_ the action!
export const setProcessTypeData = (typeData: ProcessTypeData): Action => ({
  type: 'set-process-typedata',
  typeData,
})

// A reducer must return a new object. Simply returning the same object
// (with or without modifications) does not trigger re-rendering.
// The use of immer.produce here, gives us a simple way to return a modified
// copy of the existing state, without actually mutating existing state.
export const splatReducer: Reducer<StateWithContext> = produce(
  (draft: StateWithContext, action) => {
    switch (action.type) {
      case 'set-process-state':
        debug(action.type, action.state)
        draft.state = action.state
        return
      case 'set-process-context':
        debug(action.type, action)
        draft.instanceUri = action.instanceUri
        draft.lastKnownEventId = action.lastKnownEventId
        return
      case 'set-process-typedata':
        debug(action.type, action.typeData)
        draft.typeData = action.typeData
        return
      case 'update':
        debug(action.type + ' ' + action.path + ' =>', action.value)
        set(draft.state, action.path, action.value)
        return
      case 'invoke-action':
        debug('invoke action ' + action.name)
        return
      default:
        debug('unknown action type ' + action.type)
        throw new UnknownActionError('Unknown action: ' + action.type)
    }
  }
)

export class UnknownActionError implements Error {
  constructor(message: string) {
    this.message = message
    this.name = 'UnknownActionError'
    Error.captureStackTrace(this, UnknownActionError)
  }
  name: string
  message: string
  stack?: string | undefined
}
