import {
  useSplatProcessDispatch,
  Summon,
  useSplatProcessTypeData,
  useSplatProcessTypeName,
  useSplatProcessState,
} from './SplProcess'
import { invokeAction } from '~/lib/reducers'
import React, { PropsWithChildren, useContext, FunctionComponent } from 'react'
import { Deferred } from '~/lib/backend-middleware'

type InvokeActionFn = (
  params?: Record<string, unknown>
) => Promise<ActionResponse>
export type ActionResponse = Record<string, unknown>

export const useSplatAction = (
  actionName?: string
): [boolean, InvokeActionFn] => {
  const processTypeData = useSplatProcessTypeData()
  const [, processTypeName] = useSplatProcessTypeName().split('.')
  const processState = useSplatProcessState()
  const dispatch = useSplatProcessDispatch()
  const actionNameFromCtx = useSplatActionName()
  const usedActionName = actionName || actionNameFromCtx
  let disabled = false
  if (processTypeData) {
    const processType = processTypeData[processTypeName]
    const guardPropName = processType?.actionMap[usedActionName]?.guard
    if (guardPropName) {
      disabled = !processState[guardPropName]
    }
  }
  const invoke = (params?: Record<string, unknown>) => {
    const deferredResponse = new Deferred<ActionResponse>()
    dispatch(invokeAction(usedActionName, deferredResponse, params))
    return deferredResponse.promise
  }
  return [disabled, invoke]
}

interface ComponentProps {
  name: string
}

type Props = PropsWithChildren<ComponentProps>

const SplActionNameCtx: React.Context<string> = React.createContext('???')
export const useSplatActionName: Summon<string> = () =>
  useContext(SplActionNameCtx)

export const SplAction: FunctionComponent<Props> = (props: Props) => {
  return (
    <SplActionNameCtx.Provider value={props.name}>
      {props.children}
    </SplActionNameCtx.Provider>
  )
}
